body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5; /* Light background color */
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  color: #333;
}

.button {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: #8c0cf5e7; /* Blue background */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #005bb5; /* Darker blue on hover */
}

.token-list {
  margin-top: 20px;
  background-color: white; /* White background for the token list */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 15px;
}

.token-item {
  border-bottom: 1px solid #ddd; /* Divider between tokens */
  padding: 10px 0;
}

.token-item:last-child {
  border-bottom: none; /* Remove divider for the last item */
}

.token-item span {
  display: block; /* Display token details in block format */
  color: #555; /* Darker text color for token details */
}
